header {
    background-color: #2d2d2d;
    color: #fff;
    padding: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

/* Estilos para a seção de "Nossos Diferenciais" */
.server-features {
    padding: 50px 60px; /* Adicionando padding interno à esquerda e à direita */
    background-color: #8903f0;
    text-align: center;
}

.server-features h2 {
    margin-bottom: 30px;
}

.feature {
    display: flex;
    align-items: center;
    margin-bottom: 50px;
    text-align: left;
}

.feature-content {
    flex: 1;
    text-align: left;
    padding-right: 20px;
}

.feature-content h3 {
    margin-bottom: 10px;
}

.feature-image img {
    max-width: 350px;
    border-radius: 15px;
}

/* Estilos para as imagens de carro e logo */
.car-image img,
.footer-logo img {
    max-width: 350px;
    margin-bottom: 35px;
}

nav ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: flex;
}

nav ul li {
    display: inline;
    margin: 0 10px;
}

nav ul li a {
    text-decoration: none;
    color: #fff;
}

.main-content {
    padding: 50px;
    text-align: center;
}

.main-content p {
    font-size: 18px;
}

footer {
    background-color: #2d2d2d;
    color: #fff;
    text-align: center;
    padding: 20px;
}

.footer-logo {
    margin-right: auto; /* Coloca a logo no canto esquerdo */
}

.footer-content {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: flex-end;
}

.footer-links {
    text-align: right;
}

.footer-links a {
    display: block;
    color: #ffffffc2;
    text-decoration: none;
    margin-bottom: 10px;
    font-size: 18px;
    transition: color 0.3s ease;
}

.footer-links a:hover {
    color: #8a5eff; /* roxo */
}

.footer-title {
    text-align: right;
    font-size: 20px;
    margin-bottom: 10px;
}

.footer-logo img {
    max-width: 200px; /* Ajuste o tamanho conforme necessário */
}

.btn {
    display: inline-block;
    background-color: #2d2d2d; /* rosa */
    color: #fff;
    padding: 20px 48px; /* aumentando o padding para torná-lo maior */
    border-radius: 8px; /* deixando as bordas mais arredondadas */
    border: 2px solid #ffffff; /* borda */
    text-decoration: none;
    margin-top: 20px;
    transition: background-color 0.3s ease;
}

.btn:hover {
    background-color: #8903f0; /* rosa mais escuro no hover */
}

.logo img {
    max-width: 200px;
}

.join-btn {
    border: 2px solid #8a5eff; /* borda */
    color: #fff; /* roxo */
    padding: 10px 20px;
    border-radius: 5px;
    text-decoration: none;
    box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.1);
    transition: all 0.3s ease;
}

.join-btn:hover {
    background-color: #8a5eff; /* roxo */
    color: #fff;
    box-shadow: 0px 0px 10px 0px rgba(138, 94, 255, 0.5); /* mudando a sombra para um tom de roxo mais claro */
}

